@mixin font-face($font-family, $file-path) {

    @font-face {
        font-family: $font-family;
        font-weight: 400;
        font-style: 400;
        font-display: swap;
        // src: url('#{$file-path}.eot');
        // src: url('#{$file-path}.eot?#iefix') format('embedded-opentype'),
        // url('#{$file-path}.ttf') format('truetype');
        src:
            url('#{$file-path}.woff') format('woff'),
            url('#{$file-path}.woff2') format('woff2'),;
    }
}

@include font-face('RobotoBold', '../fonts/RobotoBold/RobotoBold');

/*montserrat-regular - cyrillic*/
@font-face {
    font-family: Montserrat;
    font-weight: 400;
    font-style: 400;
    font-display: swap;
    src:
        local('Montserrat Regular'),
        url('../fonts/montserrat-v14-cyrillic-regular.woff2') format('woff2'),
        url('../fonts/montserrat-v14-cyri        lar.woff') format('woff'); /*Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+*/
